@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


/* Tela de Cadstro e Login */

a {
  text-decoration: none;
  color: #000000;
  width: 100%;
}
.content__container {  
  margin-top: 5%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  height: 100%;
}

.cadastro__btn {
  width: 50%;
}

.cadastro__btn span{
  margin-top: 5%;
}

.login__btn {
  width: 100%;
  text-align: center;
}

.login__btn span {
  margin-top: 5%;
  width: 100%;
}

.buttons_container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 35%;
}

.pagination__container {
  justify-content: center;
  background-color: rgb(238, 238, 255);
  width: 35%;
  height: 100%;

  margin-left: 0 5% 0 5%;
}

.header-btn {
  background-color: rgb(78, 78, 248);
  width: 50%;
  width: 50%;
  height: 5vh;
}

.header-btn:hover {
  cursor: pointer;
}

.header-btn-active {
  background-color:rgb(238, 238, 255);
}

.btn__text{
  display: flex;
  margin-top: 3%;
  justify-content: center;
  align-items: center;
}

.cadastro__container {
  background-color: rgb(238, 238, 255);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}


.form__container {
  align-content: center;
  margin-top: 10%;
  width: 20%;
  box-shadow: 3px 5px 4px 5px rgba(0,0,0,.5);
  border-radius: 5%;
  background-color: rgb(238, 238, 255);  
 
}

.form-content {
  
  padding: 1rem;


}

.form-content input{
  text-align: center;
  color: rgb(78, 78, 248);
  font-size: 15px;
  width: 80%;
  height: 3vh;
  border: none;
  background-color: rgb(238, 238, 255);
  border-bottom: 1px solid rgb(40,205,245);
}



.form-content input::-webkit-input-placeholder {
  color: rgb(78, 78, 248);
  text-align: center;
  background-color: rgb(238, 238, 255);
}



.form-content input:-ms-input-placeholder {
  color: rgb(78, 78, 248);
  text-align: center;
  background-color: rgb(238, 238, 255);
}



.form-content input::placeholder {
  color: rgb(78, 78, 248);
  text-align: center;
  background-color: rgb(238, 238, 255);
}

.radio-input {
  padding: 1rem;
}

.radio-input span {
  margin-right: 1rem;
}

.custom__button {
  display: inline-block;
  padding: .2rem;
  border-radius: 15%;
  border: 1px solid black;
  box-shadow: 0 2px 4px rgba(0,0,0,.5);
  margin-top: 2rem;
  margin-bottom: 1rem;
}

.custom__button:hover {
  font-size: 15px;
  cursor: pointer;
  background-color: rgb(238, 238, 255);
  box-shadow: 3px 5px 4px 5px rgba(36, 68, 251, 0.5);
}

.cadastro__title {
  margin-top: 10%;
  color:rgb(78, 78, 248);
  box-shadow: 0 1px 0px rgba(36, 68, 251, 0.5);
}

.radio__span {
  color:rgb(51, 51, 253);
  font-size: 18px;
}

.modal__error {
  position: absolute;
  top: 65%;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0 1rem;
  transition: .6s;
  border: solid rgb(209, 209, 209) 1px;
  background-color: rgb(238, 238, 255);
  box-shadow: 0 4px 8px rgb(51, 51, 253);
  height: 25%;
  width: 20%;
  border-radius: 5%;
}

.modal-icon {
  position:relative;
  font-size: 22px;
  margin-bottom: 5%;
  margin-left: 2%;
  color: red;
  top: 5px;
}

.modal__close {
  display: none;
  opacity: 0 !important;
  visibility: hidden !important;
}

.modal__error-title {
  font-size: 20px;
  color: red;
  font-weight: 600;
  margin-bottom: 5%;
}

.checked-ok {
  position: relative;
  font-size: 20px;
  left: 20px;
  top: 8px;
  color: green
}

.checked-error {
  position: relative;
  font-size: 20px;
  left: 20px;
  top: 8px;
  color: red
}
